<template>
  <div class="content"
  v-if="actualState.showChat!==false"
  >

    <!-- <div class="content-modal">
      setwtdsf;l;'l'
    </div> -->

    

    <div class="chart box" ref="chartbox">
      
      <ChatMessage
        v-for="(item, index) in chatList" :key="index"
        :message = chatList[index]
        @scrolldown="scrollDown"
      />
      
      <!-- <ImageModal
        v-if="actualState.data.chatImage"
        :chatImage = actualState.data.chatImage
        :chatImageText = actualState.data.chatImageText
      /> -->

    </div>
    <ChatQuestions
      :actualState = actualState
      :answers = answers
      :disableAnswers = "disableAnswers"
      :showButtonToChoice="showButtonToChoice"
      :btnDataChoicePatient="btnDataChoicePatient"
      @actionanswer = actionAnswer($event)
      @deleteanswer = deleteAnswer($event)
      @action = action($event)
      />

      <ImageModal
        v-if="actualState.data.chatImage"
        :chatImage = actualState.data.chatImage
        :chatImageText = actualState.data.chatImageText
    />

  </div>

</template>

<script>
import ChatMessage from "@/components/ChatMessage.vue"
import ChatQuestions from "@/components/ChatQuestions.vue"
import ImageModal from '@/components/ImageModal.vue'

export default {
  components: {
    ChatMessage,
    ChatQuestions,
    ImageModal
  },
  props: {
    chatList: [],
    actualState: {},
    answers: [],
    disableAnswers: Boolean,
    showButtonToChoice: Boolean,
    btnDataChoicePatient: {}
  },
  methods: {
    actionAnswer(action) {
      this.$emit("actionanswer", action)
    },
    deleteAnswer(index) {
      this.$emit("deleteanswer", index)
    },
    scrollDown() {
      this.$refs.chartbox.scrollTop = this.$refs.chartbox.scrollHeight
    },
    action(action){
      this.$emit("action", action)
    }
  }
}
</script>

<style>
  .content-modal{
    position: absolute;
    width: 60%;
    height: 80vh;
    left: 0;
    top: 8vh;
  }
</style>