<template>
  <div class="modal_footer"
  >
    <InfoCheckbox
      v-if="footerData.checkbox"
      :checkboxText = footerData.checkboxText
      @changecheckbox = changecheckbox($event)
    />
    <InfoButton
      v-for="(item,index) in footerData.btnData"
      :key="index"
      :btnDataItem = item
      :isDisabled="isDisabled"
      @action = buttonAction($event)
    />
  </div>
</template>

<script>
import InfoCheckbox from '@/components/InfoCheckbox.vue'
import InfoButton from '@/components/InfoButton.vue'

export default {
    data() {
      return {
        isDisabled: false
      }
    },
    components: {
        InfoCheckbox,
        InfoButton
    },
    props: {
      footerData: {}
    },
    methods: {
      buttonAction(action) {
        this.$emit("action", action)
      },
      changecheckbox(checked){
        this.isDisabled = !checked
      }
    }
}
</script>

