<template>
  <div class = "modal modal-disclaimer" style="display: flex">
    <InfoHead
      v-if="modalInfo.head"
      :head = modalInfo.head
    />
    <InfoBody
      v-if="modalInfo.body"
      :body = modalInfo.body
    />
    
    <ChoicePatient
      v-if="modalInfo.choicePatient"
      :patientList = modalInfo.choicePatient
      @action = buttonAction($event)
    />

    <div
      v-if="modalInfo.btnList"
      class="modal_body modal-btn-list">
      <ButtonList
        :btnList="modalInfo.btnList"
        @action = buttonAction($event)
      />
    </div>

    <InfoBody
      v-if="modalInfo.body2"
      :body = modalInfo.body2
    />

    <InfoFooter
      v-if="modalInfo.footer"
      :footerData = modalInfo.footer
      @action = buttonAction($event)
      :class="addClass"
    />

    <div
      class="approval-number"
      v-if="modalInfo.approvalNumber"
    >
      {{modalInfo.approvalNumber}}
    </div>

  </div>
</template>

<script>
import InfoHead from '@/components/InfoHead.vue'
import InfoBody from '@/components/InfoBody.vue'
import InfoFooter from '@/components/InfoFooter.vue'
import ButtonList from '@/components/ButtonList.vue'
import ChoicePatient from '@/components/ChoicePatient.vue'


export default {
    components: {
        InfoHead,
        InfoBody,
        InfoFooter,
        ButtonList,
        ChoicePatient
    },
    props: {
      modalInfo: {}
    },
    computed: {
      addClass(){
        return this.modalInfo.footer.class
      }
    },
    methods: {
      buttonAction(action) {
        this.$emit("action", action)
      }
    }
}
</script>

<style>
  .modal-btn-list {
    padding: 0px 40px !important;
    height: 100% !important;
    max-height: 85px !important;
  }

  .footer-auto-height {
    height: auto !important;
  }

  .approval-number{
    /* font-weight: 700; */
    color: #FFF;
    text-align: end;
  }

</style>