<template>
    <div>
        <button
        @click = buttonAction
        class="btn-to-choice"
        >
        {{btnDataItem.name}}
        </button>
    </div>
</template>

<script>

export default {
    props: {
        btnDataItem: {}
    },
    methods: {
    testTimeout(){
        setTimeout(this.buttonAction, 1000)
    },        
    buttonAction() {
      this.$emit("action", this.btnDataItem.action)
    }
  }
}
</script>

<style>
    .btn-to-choice{
        min-height: 55px;
        margin-bottom: 15px;
        font-size: 20px;
        color: #0f3a62;
        background-color: transparent;
        cursor: pointer;
        outline: none;
        text-decoration: none;
        border: 2px solid #0f3a62;
        border-radius: 10px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
    }

    .btn-to-choice:hover {
        background-color: #0f3a62;
        color: #fff;
    }
</style>