<template>
  <div :class="choiceDiv">
    <button
    @click = buttonAction
    :disabled='isDisabled'  
    :class="choiceButton"
    >{{btnDataItem.name}}</button>
  </div>
</template>

<script>
export default {
  props: {
    btnDataItem: {},
    isDisabled: Boolean
  },
  methods: {
    buttonAction() {
      this.$emit("action", this.btnDataItem.action)
    }
  },
  computed: {
    choiceDiv() {
      if (this.btnDataItem.class === "choice") {
        return "choice-div"
      }
      return ""
    },
    choiceButton() {
      if (this.btnDataItem.class === "choice") {
        return "choice-button"
      }
      return ""
    }
  },
  mounted() {
    // console.log(this.btnDataItem);
  }
}
</script>

<style>
  .choice-div{
    align-self: flex-start;
  }
  .choice-button{
    width: auto !important;
  }
</style>