<template>
  <div class="questions box" style="display: flex">
    
    <div class="header">
      <img src="../assets/images/doctor2.svg">
      <div class="title">
         :
      </div>
    </div>
        
    <div 
      v-if="actualState.type === 'answer'"
      class="body"        
      >
      <div
        v-for="(item, index) in answers"
        :key="index"
        @click = actionAnswer(index,item)
        class="question"
        :class="setDisableClass"
        v-html="item.text"
      >
      </div>
    </div>
    
    <div class="questions-footer">
      <QuestionButton
      v-if="showButtonToChoice"
      :btnDataItem="btnDataChoicePatient"
      @action="action($event)"
      :class="setDisableClass"
        />        
    </div>
  </div>
</template>

<script>
import QuestionButton from '@/components/QuestionButton.vue'

export default {
  components: {
    QuestionButton    
},
  props: {
    actualState: {},
    answers: [],
    disableAnswers: Boolean,
    showButtonToChoice: Boolean,
    btnDataChoicePatient: {}
  },
  computed: {
    setDisableClass(){
      if (this.disableAnswers) {
        return "disable-answer"
      }
      else{
        return ""
      }
    }
  },
  methods: {
    actionAnswer(index, item) {      
      this.$emit("actionanswer", item.action)
      
      if (item.deleteAnswer) {
        this.$emit("deleteanswer", index)
      }
    },
    action(action){
      this.$emit("action", action)
    }

  }
}
</script>

<style>
  .disable-answer {
    pointer-events:none;
  }

  .questions-footer{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    padding: 20px;
  }

  .questions > .body{
    overflow-y: visible !important;
  }
</style>