<template>
    
    <div
        v-if="message.isDoctor"
        class="medic"
        ref="answerDoctor"
    >
        <div class="dialog-arrow"></div>
    </div>
    
    <div
        v-else
        class="patient"
        ref="answerPatient"
    >
        <div class="dialog-arrow"></div>
    </div>

</template>

<script>
export default {
    props: {
        message: {}
    },
    mounted() {

        async function printMessageMedic(answer, messageText, thisContext) {
            answer.innerHTML = '<div class="load">...</div><div class="dialog-arrow"></div>'
            thisContext.$emit("scrolldown")
            await sleep(500)
            answer.innerHTML = messageText + '<div class="dialog-arrow"></div>'            
            thisContext.$emit("scrolldown")
            await sleep(500)
        }

        async function printMessagePatient(answer, messageText, thisContext) {
            answer.innerHTML = '<div class="load">...</div><div class="dialog-arrow"></div>'
            thisContext.$emit("scrolldown")
            await sleep(500)
            answer.innerHTML = messageText + '<div class="dialog-arrow"></div>'
            thisContext.$emit("scrolldown")
            await sleep(500)
        }
        
        if (this.message.isDoctor) {
            printMessageMedic(this.$refs.answerDoctor, this.message.text, this)
        }
        if (!this.message.isDoctor) {
            printMessagePatient(this.$refs.answerPatient, this.message.text, this)
        }

        function sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms))
        }
    }
}
</script>

<style>
/* .v-enter-active,
.v-leave-active {
  transition: opacity 2.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
} */

.fade-enter,
.fade-leave-to {
    opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
    transition: opacity 2.5s;
}

</style>