      <div
          v-if="chatImageText"
          v-html="chatImageText"
          class="chat-modal-text">        
        </div>
      
    </div>
</template>

<script>
export default {
  props: {
    chatImage: String,
    chatImageText: String
  },
  computed: {
    getImage() {
      return require(`@/assets/images/${this.chatImage}`)
    }
  },
