<template>
  <div class="container">

       <ViewChat
        v-if="showChat"
        :chatList = chatList
        :actualState = actualState
        :answers = answers
        :disableAnswers="disableAnswers"
        :showButtonToChoice="showButtonToChoice"
        :btnDataChoicePatient="btnDataChoicePatient"
        @pushmessage = pushToChatList()
        @action = doAction($event)
        @actionanswer = doAction($event)
        @deleteanswer = deleteanswer($event)
      />

      <InfoModal
        v-if="showModal"
        :modalInfo = modalInfo
        @action = doAction($event)
      />

    </div>
</template>

<script>
import InfoModal from '@/components/InfoModal.vue'
import ViewChat from '@/components/ViewChat.vue'
import MainData from '@/data/data.json'

export default {
  name: 'App',
  data() {
    return {
      patientAnswersArray: [],
      // resumeArray: [],
      stepNumber: 0,
      showModal: false,
      showImageModal: false,
      modalInfo: {},
      modalImageData: {},
      showChat: true,
      disableAnswers: false,
      answers: [],
      chatList: [],
      indexCleanChat: 0,
      mainBranch: MainData,
      actualState: {},
      showButtonToChoice: false,
      btnDataChoicePatient: {}
    }
  },
  components: {
    InfoModal,
    ViewChat
  },
  methods: {
    sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms))
        },

    async addMessages(talk, callback) {      
      for (let i = 0; i < talk.length; i++) {
        this.chatList.push(talk[i])
        await this.sleep(1000)
      }
      callback()
    },

    doAction(action) {
      if ((action.type === "next")&&(this.stepNumber < this.mainBranch.length-1)) {
        this.stepNumber += 1
        this.actualState = this.mainBranch[this.stepNumber]
        this.showModal = false
        if (this.answers.length > 0) {
          this.answers.length = 0
        }
        this.doAction(this.actualState)
      }
      else if ((action.type === "close")){
        this.showModal = false
      }
      else if ((action.type === "closeCheck")){
        this.showModal = false
        this.doAction(action.action)
      }
      else if ((action.type === "info")){
        this.modalInfo = action.data
        this.showModal = true
      }
      else if ((action.type === "answer")){
        this.actualState = action
        for (let i = 0; i < this.actualState.data.answers.length; i++) {
          this.answers.push(this.actualState.data.answers[i])
        }
      }
      else if ((action.type === "talk")){
        this.disableAnswers = true
        this.addMessages(action.data.talk, ()=> {
          if (action.data.action.type === "next"){
            this.answers.length = 0
          }
          this.disableAnswers = false
          this.doAction(action.data.action)          
          })
      }
      else if ((action.type === "checkAnswerList")){
        if (this.answers.length === 0) {
          this.doAction({"type": "next"})
        }
      }
      else if ((action.type === "jumpToStep")){
        const findStep = this.mainBranch.findIndex(step => step.id === action.stepId)
        if (findStep === -1) {
          console.log(`Error: can't find stepId "${action.stepId}" in data`)
        }
        else {
          this.stepNumber = findStep
          this.actualState = this.mainBranch[this.stepNumber]
          this.showModal = false
          if (this.answers.length > 0) {
            this.answers.length = 0
          }
          this.doAction(this.actualState)
        }
      }
      else if ((action.type === "jumpToStepClearChat")){
        this.stepNumber = this.mainBranch.findIndex(step => step.id === action.stepId)
        this.actualState = this.mainBranch[this.stepNumber]
        this.showModal = false
        this.chatList.length = 0
        if (this.answers.length > 0) {
            this.answers.length = 0
        }
        this.doAction(this.actualState)
      }
      else if ((action.type === "jumpAndClearAfterIndex")){
        this.stepNumber = this.mainBranch.findIndex(step => step.id === action.stepId)
        this.actualState = this.mainBranch[this.stepNumber]
        this.showModal = false

        this.chatList.splice(this.indexCleanChat, this.chatList.length-this.indexCleanChat+1)
        // this.indexCleanChat = 0

        if (this.answers.length > 0) {
            this.answers.length = 0
        }
        this.doAction(this.actualState)
      }
      else if ((action.type === "openLink")){
        window.open(action.url, '_blank')
      }
      else if ((action.type === "writePatientChoice")){
        this.showModal = false
        this.patientAnswersArray.push([action.questionNumber, action.answerNumber])
        this.doAction(action.action)
      }
      else if ((action.type === "runChoiceAnswers")){
        const resumeChoiceArray = this.patientAnswersArray
        resumeChoiceArray.sort((a,b) => a[0] - b[0])
        const resumeChoice = resumeChoiceArray.map(a => a[1]).join('')
        this.patientAnswersArray.length = 0
        const resumeObject = action.data.resumeArray.find(patientAnswers => patientAnswers.choiceAnswers == resumeChoice)
        this.doAction(resumeObject.action)
      }
      else if ((action.type === "showButtonToChoice")){
        this.showButtonToChoice = action.showButton
        this.btnDataChoicePatient = action.btnDataItem
        this.doAction(action.action)
      }
      else if ((action.type === "setIndexCleanChat")){
        this.indexCleanChat = this.chatList.length
        this.doAction(action.action)
      }
      
    },

    deleteanswer(index) {
      this.answers.splice(index,1)
    }
  },
  created() {
    this.actualState = this.mainBranch[this.stepNumber]
    this.doAction(this.actualState)
  }
}
</script>

