<template>
  <div class="choice-patient">
    <div
        v-for="(item,index) in patientList"
        :key="index"
        @click="buttonAction(item)"
        class="patient-card"
    >
        <div
            v-html="item.head"
            class="patient-head">            
        </div>
        <div class="choice-div-image">
            <img :src="getImage(index)" alt="choice patient" class="choice-image">
        </div>
        <div class="patient-footer">
            <div class="patient-footer-text">
                {{item.text}}
            </div>
            
        </div>
    </div>
  </div>

</template>

<script>
export default {
    props: {
        patientList: []
    },
    methods: {
        getImage(index) {
            return require(`@/assets/images/${this.patientList[index].image}`)
        },
        buttonAction(item) {
            this.$emit("action", item.action)
        }
    }
}
</script>

<style>
    .choice-patient {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100% !important;
        overflow: auto;
    }
    .patient-card {
        width: 330px;
        height: 500px;
        display: flex;
        flex-direction: column;
        text-align: center;
        cursor: pointer;
        background: #FFFFFF;
        opacity: 0.8;
        border-radius: 10px;
        margin: 15px;
        color: #0F3A62; 
    }
    .patient-card:hover {
        background: #0F3A62;
        opacity: 0.8;
        color: #FFFFFF;
    }
    .patient-head {
        font-family: 'Open Sans', sans-serif;
        font-style: normal;
        font-weight: 700 !important;
        font-size: 24px !important;
        line-height: 33px;
        text-align: center;
        color: #CC3D66 !important;
        margin-bottom: 20px;
        margin-top: 20px;
    }
    .patient-footer{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-grow: 1;
        padding-left: 75px;
        padding-right: 75px;
    }
    .patient-footer-text {
        font-family: 'Open Sans', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;  
    }

    .choice-div-image{
        width: 178px;
        height: 186px;
        display: flex;
        width: 100%;
    }
    .choice-image{
        width: 100%;
        height: auto;
    }
</style>